@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial: 12px;
  --font-size-lv2: 14px;
  --font-size-lv3: 16px;
  --font-size-lv4: 18px;
  --spacing-site: 1rem;
  --color-text-main:#000; }

* {
  padding: 0;
  margin: 0; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

@media screen and (min-width: 600px) {
  html {
    font-size: var(--font-size-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 16px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 18px); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em;
  font-weight: bold; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 2em; }

li {
  list-style-position: inside; }

.site-header {
  background: #141414;
  color: red;
  padding: 3rem 0; }

.site-branding {
  margin: 0;
  padding: 0; }

.site-header p {
  padding: 0;
  margin: 0; }

.site-title {
  font-size: 2em; }

.site-description {
  font-style: italic;
  letter-spacing: 0.03em; }

.site-footer {
  background: #141414;
  color: white;
  text-align: center; }

.site-footer form {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 auto;
  width: 50%; }

.site-footer input {
  outline: none;
  border: none;
  border-radius: 20px;
  padding: 10px;
  margin-bottom: 2%;
  font-weight: bold;
  color: black;
  background: white; }

.site-footer input:last-child {
  cursor: pointer;
  background: #f8794b;
  width: 50%;
  align-self: center; }

.copyrights {
  background: black;
  padding-top: 1rem; }

.site-header {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.navigation ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  flex-wrap: wrap;
  background: #ABABAB;
  margin-right: 1em; }

.navigation li {
  display: block;
  list-style: none outside;
  padding: 1em 0;
  margin-right: 1em; }

.navigation a {
  display: block;
  padding: 1em 2em;
  text-decoration: none;
  color: black;
  font-weight: bold;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.25); }

.information {
  background: url("../img/table.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center top;
  padding: 1rem;
  padding-top: 10%;
  padding-bottom: 5%;
  color: #fff;
  background-size: cover;
  text-align: center; }

.information h3, .information p, .information form {
  max-width: 60rem;
  margin: 0 auto; }

.information form {
  padding-top: 10%;
  display: flex;
  justify-content: center;
  align-content: center;
  vertical-align: baseline; }

.information form input {
  margin-right: 1rem;
  width: 20rem;
  outline: none;
  border: none;
  border-radius: 20px;
  padding: 10px;
  background: black;
  color: white;
  font-weight: 400; }

.posts ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0; }

.posts ul li {
  list-style: none;
  background: #ABABAB;
  align-items: center;
  text-align: center;
  box-shadow: inset 0 0 0 10px #141414, inset 0 0 0 12px white, 0 6px 20px red; }

.posts ul li a {
  text-decoration: none;
  font-size: 1.5rem;
  color: black; }

.posts ul li p {
  padding: 1.5rem 3rem;
  font-size: 1.3rem;
  font-weight: 400; }

.livescore {
  background: #141414;
  padding-top: 5rem; }

.livescore-message {
  width: 60%;
  margin: 0 auto;
  padding: 3rem 0;
  background: white;
  text-align: center; }

.rules {
  background: #cabfbf;
  padding: 1% 5%; }

.rules, .rules h3, .rules h4 {
  text-align: center; }

.rules article {
  text-align: left; }

.rules span {
  font-weight: bold; }

.rules img {
  width: 60%;
  height: auto; }

.rankings {
  background: lightgray;
  padding: 3rem 0; }

table {
  width: 60%;
  table-layout: fixed;
  border: 2px solid black;
  border-spacing: 0;
  border-collapse: collapse;
  text-align: center;
  margin-left: 20%;
  margin-right: 20%;
  text-align: center;
  background: white;
  font-size: 1.3rem; }

table td, table th {
  border: 1px solid black;
  padding: 1rem 2rem; }

table thead th {
  text-align: center;
  background: #1e1e1e;
  color: white;
  font-size: 1.5rem;
  font-weight: bold; }

table tr:hover td {
  background: black;
  color: #FFF;
  border-color: transparent; }

[data-label="RANK"] {
  background: #eb003b;
  font-weight: bold; }

[data-label="AMOUNT"] {
  font-style: italic; }

@media screen and (max-width: 800px) {
  table {
    text-align: center; }
  table thead {
    display: none; }
  table, table tbody, table tr, table td {
    display: block; }
  table tr {
    border: 2px solid black; }
  table tbody td:before {
    display: inline-block;
    width: 10rem;
    font-weight: bold;
    text-align: right;
    padding-right: 1rem;
    white-space: nowrap;
    content: attr(data-label); } }

.login {
  display: flex;
  justify-content: center;
  align-items: center;
  background: lightgray; }

.sign {
  border-right: 2px solid grey;
  padding: 0 2rem; }

.sign section {
  width: 100%;
  font-size: 1.2rem;
  margin-bottom: 1rem;
  padding-right: 1rem; }

.sign section a {
  align-items: center;
  color: white;
  display: flex;
  height: 2.5rem;
  text-decoration: none; }

.sign section span {
  align-items: center;
  align-self: stretch;
  display: flex;
  font-size: 1.3rem;
  justify-content: center;
  margin-right: 1rem;
  text-align: center;
  width: 2.5rem;
  color: white; }

.facebook {
  background: #3b5ca1; }

.facebook span {
  background: #314d87; }

.twitter {
  background: #44aee1; }

.twitter span {
  background: #26a2dd; }

.google {
  background: #de4c33; }

.google span {
  background: #cc3a21; }

.login-forms {
  padding: 0 2rem; }

.login-forms input {
  width: 100%;
  font-size: 1.2rem;
  margin-bottom: 1rem;
  padding-right: 1rem;
  height: 2.5rem;
  outline: none;
  border: none; }

.login-forms .user input, .login-forms .pass input, .login-forms .sign-btn input {
  border-radius: 0.7rem;
  background: #1e1e1e;
  text-align: center; }

.login-forms input::placeholder {
  color: white; }

.login-forms .sign-btn input {
  background: lightgreen; }

body > div > main > h3 {
  text-align: center;
  background: lightgray; }

.players-gallery {
  width: 100%;
  background: linear-gradient(to right, #d4e4ef 0%, #ababab 100%); }

.card-gallery {
  width: 100%;
  margin: 0 auto;
  padding: 2rem 1rem;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center; }

.player-pic {
  width: 28%;
  border: 1.5px solid #ccc;
  padding: 1rem;
  margin: 2% 1.5%;
  flex-grow: 0;
  background-color: #ecf0f1f6; }

.player-pic img {
  max-width: 100%;
  margin: 0 auto; }

.player-pic:hover {
  box-shadow: 0 1px 5rem rgba(0, 0, 0, 0.5);
  transform: scale(1.2);
  border: none;
  transition: box-shadow 0.3s ease, transform 0.3s ease;
  z-index: 1; }

.player-pic p {
  padding: 0.4rem; }

